import React from 'react';
import { Route, Routes } from 'react-router-dom';
import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './Components/Home';
import Remove from './Components/Remove';
import Success from './Components/Success';
import Navbar from './Components/Navbar';
import Nomatch from './Components/Nomatch';
import { useContext } from "react";
import { context } from "./Components/ThemeContext";
import notify from './Components/NotificationContext';
import DismissibleExample from './Components/DismissibleExample';
import gsap from 'gsap';
import './styles/global.css'; 
function App() {
  const { theme } = useContext(context);
  const { show } = useContext(notify);

  return (
    <div className='main' style={{ fontFamily: 'Josefin Sans, sans-serif' }}>
      {show && <DismissibleExample showA={show} />}
      <Navbar />
    
      <div className="App">
        
          <Routes>
            <Route path="/Duplicator" element={<Home />} />
            <Route path="/Duplicator/Remove" element={<Remove />} />
            <Route path="/Duplicator/History" element={<Success />} />
            <Route path="*" element={<Nomatch />} />
          </Routes>
    
      </div>
    </div>
  );
}

export default App;
