import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Toast from 'react-bootstrap/Toast';
import '../styles/global.css'; 

import { useContext,useRef,useEffect } from "react"
import { context } from "./ThemeContext"
import gsap from 'gsap/all';
function DismissibleExample(prop) {
  const {theme, setTheme}=useContext(context)

  const [showA, setShowA] = useState(prop.showA);  
  const toggleShowA = () => setShowA(!showA);
  const boxRef = useRef(null);

  // Animation trigger based on 'show'
  useEffect(() => {
    if (showA) {
      gsap.to(document.querySelector('.notice'), {
        duration: 2,
        y: '170%',
        ease: 'bounce.out',
      });
    }
    else if (!showA) {
      gsap.to(document.querySelector('.notice'), {
        duration: 2,
        y: '-10%',
        ease: 'bounce.out',
      });
    }
  }, [showA]); // Corrected dependency array

  
  return (
    <div className='notice' style={{fontFamily: 'Josefin Sans, sans-serif' ,color:theme&&'hsl(220, 15%, 55%)',backgroundColor: theme? 'hsl(212, 45%, 89%)':'hsl(0, 0%, 8%)'}}>
     
        <Toast show={showA} className='w-sm-50' onClose={toggleShowA}>
          <Toast.Header>
            <img
              src="holder.js/20x20?text=%20"
              className="rounded me-2"
              alt=""
            />
            <strong className="me-auto">Notification</strong>
    
          </Toast.Header>
          <Toast.Body >All duplicates has been removed!</Toast.Body>
        </Toast>
      
      
    </div>
  );
}

export default DismissibleExample;