import { useState, useEffect, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import DismissibleExample from "./DismissibleExample";
import Audio from "./Audio";
import { useContext } from "react"
import { context } from "./ThemeContext"
import cancel from "./images/icon-cross.svg"
import { gsap } from 'gsap';
import notify from "./NotificationContext";
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import '../styles/global.css'; 

function Remove() {
  const navigate = useNavigate()
  const location = useLocation();
  const state = location.state || {};
  const { show, setShow } = useContext(notify)
  const { input } = state;
  const [noDulicates, setDuplicates] = useState(false)
  const [arrInput, setArrInput] = useState(() => {
    const storedArray = localStorage.getItem("arrInput");
    return storedArray ? JSON.parse(storedArray) : [];
  });

  const handleClose = () => setShows(false);

  const [originalString, setOriginal] = useState(input)
  const [modifiedString, setModified] = useState("")
  // Save the array to local storage whenever it changes
  useEffect(() => {
    localStorage.setItem("arrInput", JSON.stringify(arrInput));
    setModified(arrInput.join('')); // Ensure the modified string reflects the current state of `arrInput`
  }, [arrInput]); // Dependency array to track changes in `arrInput`

  function hasDuplicates(array) {
    // Create a set of unique elements
    const uniqueItems = new Set(array);
    return array.length == uniqueItems.size; // True if there are duplicates
  }
  useEffect(() => {
    const hasDupes = hasDuplicates(arrInput); // Determine if there are duplicates
    setDuplicates(hasDupes); // Update `noDulicates` accordingly
    setShow(hasDupes)

  }, [arrInput]); // Dependency array ensures this runs when `arrInput` chang
  function handleDuplicate(item, clickedIndex) {
    // Keep only the item with the specified `clickedIndex`
    setShows(modal(item, clickedIndex, arrInput))
    const newArr = arrInput.filter((el, index) => !(el === item && index !== clickedIndex));

    setArrInput(newArr);

  }
  useEffect(() => {
    const firstElement = document.querySelector('.characters:first-child'); // Select the first 'characters' element
    if (firstElement) {
      firstElement.style.borderTopRightRadius = '0.3cm'; // Set top-right border radius
      firstElement.style.borderTopLeftRadius = '0.3cm'; // Set top-right border radius
      firstElement.style.borderColor = 'transparent'; // Set top-right border radius
    }
  }, []); // Run once on component mount
  
  function modal(item, clickedIndex, newArr) {
    return !newArr.find((el, index) => (el === item && index !== clickedIndex));
  }
  const { theme, setTheme } = useContext(context)
  const style1 = {
    backgroundColor: theme ? 'hsl(0, 0%, 98%)' : 'hsl(235, 24%, 19%)',
    color: theme && 'hsl(236, 9%, 61%)'
  }
  const [shows, setShows] = useState(false);
  const animate= ()=>{
      gsap.to(
        document.querySelectorAll('.characters'),{
        duration: 2,
        y: '100%',
        ease: 'bounce.out',
        stagger: 0.2,
      }
      )
  }
  useEffect(()=>{
    window.addEventListener('load',animate())
  },[])
  const style2 = {
    backgroundColor: theme ? 'hsl(236, 33%, 92%)' : 'hsl(235, 21%, 11%)',
    color: "white",


  }
  const words = arrInput.map((item, index) => (
    <div
      key={index}
      style={{...style1, marginTop:''}}
      className="characters"
    >
      <div>
        <h5 className="item"> {item}</h5>
      </div>
      <div>
        <h5 className="item">index #{index}</h5>
      </div>
      <div>
        <img onClick={() => handleDuplicate(item, index)} src={cancel} style={{ cursor: "pointer" }} alt="delete" />
      </div>
    </div>
  ));

  function handleClick() {
    navigate(-1)
    setShow(false)
  }
  return (
    <div style={{ fontFamily: 'Josefin Sans, sans-serif' }}>
      

      <>


        <Modal show={shows} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title className='text-danger'>Error!</Modal.Title>
          </Modal.Header>
          <Modal.Body>There are no duplicates of the letter!</Modal.Body>
          <Modal.Footer>
            <Button variant="danger" onClick={handleClose}>
              Close
            </Button>

          </Modal.Footer>
        </Modal>
      </>

      {noDulicates && <Audio />}
      <div className="for" style={style2}>
        
        <div className="word"  >
          <div className="words characters" style={{ ...style1,color: 'white' , padding: '1.5%'}}>
            <div>
              <h6 className="string">Original String: {originalString}</h6>
            </div>
            <div>
              <h6 className="string">Modified String:{modifiedString}</h6>
            </div>
          </div>
          {words}
          <button onClick={handleClick} className="button characters" style={{ ...style1, paddingTop: '2px' , marginTop:'1%', justifyContent:'center', borderBottomLeftRadius:'0.3cm', borderBottomRightRadius:"0.3cm"}}>Go back to homepage</button>
        </div>


      </div>
    </div>
  );
}

export default Remove;
