import { useContext, useEffect, useState } from "react";
import { context } from "./ThemeContext";
import { useNavigate } from "react-router-dom";

function Success() {
    const [history, setHistory] = useState([]);
    const navigate = useNavigate()
    useEffect(() => {
        try {
            const storedData = localStorage.getItem("allInputs");
            if (storedData) {
                const parsedData = JSON.parse(storedData);
                if (Array.isArray(parsedData)) { // Check if the parsed data is an array
                    setHistory(parsedData); // If it's an array, set it to state
                } else {
                    throw new Error("Stored data is not an array"); // Throw an error if it's not an array
                }
            }
        } catch (error) {
            console.error("Error retrieving or parsing data from local storage:", error);
            setHistory([]); // Default to empty array
        }
    }, []); // Empty dependency array to ensure this effect runs once on component mount
    const { theme, setTheme } = useContext(context)
    const style1 = {
        backgroundColor: theme ? 'hsl(0, 0%, 98%)' : 'hsl(235, 24%, 19%)',
        color: theme ? 'hsl(236, 9%, 61%)' : 'white'
    }
    
    const data = history.length > 0
        ? history.map((item, index) => (
            <div key={index} style={style1} className="characters">
                <div>
                    <h6 className="character">{index + 1}</h6>
                </div>
                <div>
                    <h6 className="character">{item}</h6>
                </div>
            </div>
        ))
        : <h6 style={{ ...style1, textAlign: 'center' }}>No history</h6>; // Render "No history" if `history` is empty
    const style2 = {
        backgroundColor: theme ? 'hsl(236, 33%, 92%)' : 'hsl(235, 21%, 11%)',
        color: theme ? 'hsl(236, 9%, 61%)' : 'white'


    }
    return (
        <div style={style2} className="paged">
            <div className="History" style={style1}>
                <div style={{display:'flex', flexDirection: 'row',justifyContent:'center'}}>
                        <h1>History</h1>
                    </div>
                <div className="title" style={{alignItems:'center'}}>
                    
                    <div>
                        <h5 className="number">Number of words inputed={history.length}</h5>
                    </div>
                    <div style={{display:'flex', alignItems:'center'}}>
                        {history.length > 0 &&
                            <h5
                                className="button"
                                style={{...style1, border:0}}
                                onClick={() => {
                                    localStorage.setItem("allInputs", ""); // Clear local storage
                                    setHistory([]); // Clear `history` state
                                }}
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" width="15" height="16" fill="currentColor" class="bi bi-trash3-fill" viewBox="0 0 16 16">
                                <path d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5m-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5M4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06m6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528M8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5"/>
                                </svg>
                                &nbsp;
                                Clear history
                            </h5>
                        }
                    </div>
                </div>
                <div>
                    <div>
                        {data}
                        <button style={style1} className="button" onClick={() => { navigate('/Duplicator') }}>Go back to homepage</button>

                    </div>
                </div>

            </div>
        </div>
    );
}

export default Success;
