import React, { useEffect } from 'react';
import { Howl } from 'howler';
const myAudio = `${process.env.PUBLIC_URL}/audio/josh_hutcherson_whistle_instrumental_mp3_57819.mp3`;

function Audio(){
    useEffect(() => {
        const sound = new Howl({
          src: [myAudio],
          autoplay: true, // Automatically play the sound
          loop: false,    // Change to true if you want it to loop
          volume: 0.5,     // Adjust the volume level
    
        });
        const timer = setTimeout(() => {
            sound.stop(); // Stop playing after 5 seconds
          }, 3000); // 5000 milliseconds = 15 seconds
        return () => {
          sound.stop(); // Stop the sound when the component unmounts
        };
      }, []);
      return(
        <div></div>
      )
}
export default Audio